import React, {useEffect, useState,useRef} from "react";
import { dbService } from "../fbase";
import {collection,  addDoc } from "firebase/firestore";
import '../css/addcom.css';

function AddPincom({mLoc, userObj, flow, setFlow,imgUrl }) {
  //사용자명에 유즈래프 ! ! 해서 엔터쳐도 가능하게 만들기
  
  const [inputs, setInputs] = useState({
    nickname: '',
    commen: ''
  });
  const { nickname,commen } = inputs; // 비구조화 할당을 통해 값 추출
  const [cVisible, setCVisible ]= useState(false);
  const inputRef = useRef([]);

    useEffect(()=> { // 사용자명 커서 포커스
      if(flow === 2) inputRef.current[0].focus(); 
      if(flow === 3) inputRef.current[1].focus(); 
    },[flow]);

    const onChange = (e) => { //사용자명이랑 핀댓글 내용 바뀌는거 
      const { name, value } = e.target; //타겟에서 네임이랑 벨류 추출
      setInputs({
        ...inputs, // 기존의 input 객체를 복사한 뒤
        [name]: value // name 키를 가진 값을 value 로 설정
      });
    };

    const onNick = (e) => {
      if (nickname === "") {
        return;
      } 
      setCVisible(true);
      setFlow(3);
      };

    const handleOnKeyPress = e => { // 인풋에 적용할 Enter 키 입력 함수
        if (e.key === 'Enter') {
          e.preventDefault();
           onNick(); // Enter 입력이 되면 클릭 이벤트 실행       
        }
    };
      
    const onSubmit = async(event) => {
        if (commen === "") {
            return;
          }
    event.preventDefault();
    try {
      const pincomObj = { //트윗 오브젝트
        nickname, 
        commen,
        date: Date.now(),
        creatorId: userObj.uid,
        x: mLoc.x,
        y: mLoc.y,
        imgUrl : imgUrl[mLoc.n-1],
        likeN : 0,
        report : false
     };

     await addDoc(collection(dbService, "comments"), pincomObj); 
     //트윗하기 누르면 pincomObj 형태로 새로운 document 생성하여 comments 콜렉션에 넣기
    } catch (e) {
    console.error("Error adding document: ", e);
    }
    setInputs({
      nickname: '',
      commen: '',
    });
    setFlow(0);
    };


  return(
    <>
      {!cVisible ?
      <div className="inputForm">
         <input
             className="text_input"
             name="nickname"
             value={nickname}
             onChange={onChange}
             onKeyPress={handleOnKeyPress}
             type="text"
             placeholder="사용자명"
             maxLength={10}
            ref={el =>(inputRef.current[0] = el)}
           />
           <button onClick={onNick} >다음</button>
      </div>  :  
      <form  onSubmit={onSubmit} className="inputForm">
       <input
          className="text_input"
          name="commen"
          value={commen}
          onChange={onChange}
          type="text"
          placeholder="핀댓글을 입력해주세요"
          maxLength={120}
          ref={el =>(inputRef.current[1] = el)}
        />
        <button type="submit">등록</button>
      </form>}
    </>
  )
};

export default AddPincom;
